export default {
	data() {
		return {
			isListShow: false,
			list:[
				{
					name:'产品中心',
					url: '/pages/product/product',
					type: true
				},
				{
					name:'商城开发',
					url: '/pages/shop/shop',
					type: true
				},
				{
					name:'百度小程序',
					url: '/pages/baidu/baidu',
					type: false
