					url: '/pages/concact/concact',
					type: false
				},
			],
		}
	},
	methods: {
		// 控制导航nav的显示或者隐藏
		handleListShow() {
			this.isListShow = !this.isListShow
		},
		// 实现页面的跳转
		handleType(options) {
			this.isListShow = false
			if(!options.type) {
				uni.navigateTo({
					url:options.url
				})
			}
			else {
				uni.switchTab({
					url:options.url
				})
			}	
		}
	}
